section.banner {
  padding: 5em 1em;
}

section.banner h1 {
  color: #1A2930;
  text-align: right;
  font-size: 5em;
  display: inline;
  float: right;
  margin: 0;
}

.aboutMe {
  position: absolute;
  z-index: 1000;
  right: 20%;
}

input#btn-state {
  position: absolute;
  visibility: hidden;
  height: 1px;
  width: 1px;
  left: -1px;
  top: -1px;
}

.morph {
  background-image: url(../images/about-me-logo.svg);
  background-size: 70% 70%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #2978A0;
  border-radius: 2.5rem;
  width: 5rem;
  height: 5rem;
  margin: 50px auto;
  text-align: center;
  transition: all .3s;
  box-shadow: 1px 4px 11px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

.morph:hover {
  background-color: #347FC4;
}

.btn-morph-on {
  display: block;
  height: 100%;
  width: 100%;
  color: #FFF;
  font-weight: 700;
  line-height: 1.8em;
  font-size: 2.5em;
  cursor: pointer;
  z-index: 1001;
}

.btn-morph-off {
  position: relative;
  top: 15px;
  left: 40%;
  color: #FFF;
  display: none;
  font-size: 2.5em;
  cursor: pointer;
}

.morph-content {
  padding: 40px;
  padding-top: 10px;
  color: #FFF;
  font-size: 1.3em;
  line-height: 1.5em;
  font-weight: light;
}

.morph-content .headline {
  text-align: center;
}

#btn-state:checked + .morph {
  background-image: url(../images/about-me-logo-transparent.svg);
  background-color: #347FC4;
  width: 500px;
  height: 570px;
  border-radius: 7px;
  transition: all .3s;
}

#btn-state:checked + .morph .btn-morph-on {
  display: none;
}

#btn-state:checked + .morph .btn-morph-off {
  display: block;
}

@media (max-width: 1100px) {
  section.banner {
    padding: 5em 6em;
  }
  section.banner h1 {
    font-size: 3em;
  }
  section.banner .aboutMe {
    right: 12%;
  }
}

@media (max-width: 725px) {
  section.banner {
    padding: 3em 4.5em;
  }
  section.banner .logo {
    width: 80px;
    height: 60px;
  }
  section.banner h1 {
    font-size: 2.5em;
  }
}

@media (max-width: 600px) {
  section.banner {
    padding: 2em 3.5em;
  }
  section.banner h1 {
    font-size: 2em;
  }
  section.banner .aboutMe {
    margin: 10px 0;
    right: 9%;
    top: 11%;
  }
  section.banner .morph-content {
    padding: 20px;
    padding-top: 0;
    font-size: 1.1em;
    line-height: 1.3em;
  }
  section.banner .morph-content .headline {
    word-spacing: 5px;
  }
  section.banner #btn-state:checked + .morph {
    width: 400px;
    height: 600px;
    transition: all .3s;
  }
}

@media (max-width: 485px) {
  section.banner {
    padding: 1.4em 2.5em;
  }
  section.banner .logo {
    width: 60px;
    height: 40px;
  }
  section.banner h1 {
    font-size: 1.6em;
  }
  section.banner .aboutMe {
    top: 7%;
  }
  section.banner #btn-state:checked + .morph {
    width: 300px;
    height: 570px;
    transition: all .3s;
  }
}

section.projects {
  padding: 5em;
  background: #1A2930;
}

section.projects .wrapper {
  display: flex;
  flex-direction: column;
  flex-flow: row wrap;
}

section.projects .card {
  height: 450px;
  margin-bottom: 2em;
  border-radius: .35em;
  background: #ffffff;
  overflow: hidden;
  transform: scale(1, 1);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.45);
  transition: all 0.2s ease-in-out;
}

section.projects .card:hover {
  transform: scale(1.01, 1.01);
  box-shadow: 0 7px 17px rgba(0, 0, 0, 0.75);
}

section.projects .card_full {
  flex-basis: 100%;
}

section.projects .card_half {
  flex-basis: 48.5%;
}

section.projects .card_third {
  flex-basis: 75%;
}

section.projects .left {
  margin-right: 1.5%;
}

section.projects .right {
  margin-left: 1.5%;
}

section.projects .tall {
  height: 600px;
}

section.projects .card_inner {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  padding: 8vh 60px;
  color: #ffffff;
}

section.projects .card_inner p {
  font-size: 1em;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .2em;
}

section.projects .card_inner h2 {
  font-size: 3em;
  margin: 0;
  letter-spacing: .1em;
}

section.projects .see-online {
  margin-top: 2em;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

section.projects .see-online a.button {
  background: transparent;
  padding: 1em 1.5em;
  text-align: center;
  border: 2px solid #fff;
  border-radius: .5em;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: .8em;
  font-weight: 700;
  transition: all .3s ease-in-out;
}

section.projects .see-online a.button:hover {
  border: 2px solid #343e48;
  color: #343e48;
}

section.projects .see-online a.github {
  position: absolute;
  bottom: 1.5em;
  color: #343e48;
  text-decoration: none;
  margin: .5em 1em;
  text-align: center;
}

section.projects .see-online a.github:hover {
  color: gray;
}

section.projects .see-online a.github:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0;
  left: 0;
  background-color: gray;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

section.projects .see-online a.github:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

section.projects .yellow, section.projects .orange, section.projects .purple, section.projects .green {
  border-radius: .35em;
}

section.projects .yellow {
  background: rgba(252, 192, 44, 0.9);
}

section.projects .orange {
  background: rgba(255, 59, 63, 0.9);
}

section.projects .purple {
  background: rgba(203, 135, 239, 0.9);
}

section.projects .green {
  background: rgba(78, 197, 193, 0.9);
}

section.projects .view_more {
  flex-basis: 25%;
  height: 450px;
  padding-left: 3em;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

section.projects .view_more a {
  text-decoration: none;
}

section.projects .view_more a:hover p {
  color: #FF3B3F;
}

section.projects .view_more a p {
  font-size: 1.25em;
  font-weight: 700;
  letter-spacing: .1em;
  color: #9EACB2;
}

section.projects .card_1 {
  background-image: url("../images/photo.svg");
  background-size: 55%;
  background-position: 520px -90px;
  background-repeat: no-repeat;
}

section.projects .card_2 {
  background-image: url("../images/icons.png");
  background-size: 120%;
  background-position: 10px 194px;
  background-repeat: no-repeat;
  background-repeat: space;
}

section.projects .card_3 {
  background-image: url("../images/react-firebase-1.svg");
  background-size: 100%;
  background-position: 48px 120px;
  background-repeat: no-repeat;
}

section.projects .card_4 {
  background-image: url("../images/currencies.svg");
  background-size: 99%;
  background-position: 5px -20px;
  background-repeat: no-repeat;
  background-repeat: repeat;
}

@media (max-width: 1100px) {
  section.projects .card_inner h2 {
    font-size: 2.2em;
  }
  section.projects .card_1 {
    background-position: 420px -50px;
  }
}

@media (max-width: 850px) {
  section.projects .card_inner h2 {
    font-size: 1.75em;
  }
  section.projects .card_1 {
    background-position: 340px -30px;
  }
}

@media (max-width: 725px) {
  section.projects {
    padding: 4em;
  }
  section.projects .card {
    height: 350px;
  }
  section.projects .view_more {
    height: 200px;
  }
  section.projects .card_half, section.projects .card_third, section.projects .view_more {
    flex-basis: 100%;
  }
  section.projects .left {
    margin-right: 0px;
  }
  section.projects .right {
    margin-left: 0px;
  }
  section.projects .card_1 {
    background-position: 300px -30px;
  }
  section.projects .card_2, section.projects .card_3 {
    background-size: 70%;
    background-position: 145px 50px;
  }
}

@media (max-width: 650px) {
  section.projects {
    padding: 3em;
  }
  section.projects .card {
    height: 250px;
  }
  section.projects .card:hover {
    transform: scale(1, 1);
  }
  section.projects .view_more {
    height: 100px;
  }
  section.projects .card_inner {
    padding: 4vh 60px;
  }
  section.projects .card_inner h2 {
    font-size: 1em;
  }
  section.projects .card_inner .see-online {
    margin-top: 0;
  }
  section.projects .card_inner .see-online a.button {
    padding: .8em 1.2em;
  }
  section.projects .card_inner .see-online a.github {
    margin: 1em 2.7em;
    bottom: 2em;
    left: 1em;
  }
}

@media (max-width: 480px) {
  section.projects {
    padding: 1.6em;
  }
  section.projects .card {
    margin-bottom: 1em;
  }
  section.projects .view_more {
    padding-left: 30px;
  }
  section.projects .view_more a, section.projects .view_more p {
    font-size: .9em;
  }
  section.projects .card_inner {
    padding: 4vh 30px;
  }
  section.projects .card_inner p.category {
    font-size: .8em;
  }
  section.projects .card_inner h2 {
    margin-bottom: 15px;
  }
  section.projects .card_1 {
    background-position: 170px -20px;
  }
  section.projects .card_2, section.projects .card_3 {
    background-position: 95px 50px;
  }
}

@media (max-width: 400px) {
  section.projects .card .card_inner .see-online a.github {
    max-width: 82px;
    bottom: 1em;
  }
}

footer {
  padding: 3em 1em;
}

.footer-list {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0;
}

.footer-list li .title {
  font-size: .95em;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  padding-top: .5em;
  padding-bottom: .4em;
  text-align: center;
  letter-spacing: 2px;
}

.footer-list li .links {
  font-size: 1.5em;
}

.footer-list li .links a {
  color: #1A2930;
  text-decoration: none;
}

.footer-list li .links a .footer-social-media {
  width: 30px;
  margin: 0 .5em;
  fill: #1A2930;
}

.footer-list li .links a .footer-social-media:hover {
  fill: #FF3B3F;
}

@media (max-width: 1100px) {
  footer {
    padding: 5em 6em;
  }
}

@media (max-width: 725px) {
  footer {
    padding: 3em 4.5em;
  }
  footer .footer-list {
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-end;
    margin: 0;
  }
  footer .footer-list li {
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  footer .logo {
    width: 80px;
    height: 60px;
  }
}

@media (max-width: 600px) {
  footer {
    padding: 2em 3.5em;
  }
}

@media (max-width: 485px) {
  footer {
    padding: 1.4em 2.5em;
  }
  li.logo {
    width: 60px;
    height: 40px;
  }
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Dosis', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
}

.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}

.clearfix::after {
  clear: both;
}

.wrapper {
  width: 1000px;
  max-width: 100%;
  margin: 0 auto;
}

.logo {
  width: 100px;
  height: 80px;
  background-image: url(../images/logo-port.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  float: left;
  margin-left: 0px;
}
